<!-- by 添少 -->
<template>
  <fixed-layout>
    <template slot="header">
         <v-Header class="head">
          <span slot='text' class="headText">选择{{title}}</span>
          <mt-button class="nextBtn" @click="handNext" slot="right">下一步</mt-button>
        </v-Header>
    </template>
     
    <div class="club">
      <div class="item hd">
        <span>选择{{title}}</span>
        <span>{{(list[selectedIndex]||{tagName: '请选择'}).tagName}}</span>
      </div>
      <template v-for="(row,$index) in list">
        <div class="item" :class="{active:$index===selectedIndex}" @click="handSelect($index)">
          <div>{{row.tagName}}</div>
          <div><b class="iconfont icon-success"></b></div>
        </div>
      </template>
    </div>
  </fixed-layout>
</template>
<script>
import Header from '../../components/header';
  import FixedLayout from '@/components/fixed-layout'
  import Topbar from '@/components/topbar'

  export default {
    name: '',
    components: {
      Topbar,
      FixedLayout,
      'v-Header': Header,
    },
    props: {},
    data() {
      return {
        title: '讨论区',
        selectedIndex: -1,
        list: []
      }
    },
    watch: {},
    computed: {},
    created() {
      this.init()
    },
    methods: {
      init() {
        // 初始化缓存
        this.$store.commit(this.$actionType.set_post_data, {
          tagId: 0,
          clubId: 0
        })
        this.$http.get(this.$port.DISCUSS_TAG_PROT).then(res => {
          this.list = res.data
        })
      },
      initData() {
      },
      handSelect(index) {
        this.selectedIndex = index
      },
      handNext() {
        this.$store.commit(this.$actionType.set_post_data, {
          tagId: this.list[this.selectedIndex].tagId
        })
        this.$http.get(this.$port.MY_CLUB, {
          params: {
            auditStatus: 2
          }
        }).then(res => {
          if (res.code === 200) {
            if (res.data.length > 1) {
              this.$router.push({
                path: '/post/club',
              })
            } else {
              this.$router.push({
                path: 'edit',
              })
            }
          }
        })
      }
    }
  }
</script>
<style lang="scss">

</style>
<style lang="scss" scoped>
  .club {
    background-color: #fff;
    font-size: 16px;
    width: 100%;
    margin-top: 10px;
    .item {
      display: flex;
      border-bottom: solid #e5e5e5 1px;
      > * {
        padding: 12px 15px 13px;
        flex: 1;
      }
      > :last-child {
        text-align: right;
        color: #3aa7cf;
        visibility: hidden;
      }
      .iconfont {
        font-size: 16px;
      }
      &.active {
        background-color: #3aa7cf;
        color: #fff;
        > :last-child {
          color: #fff;
          visibility: visible;
        }
      }
    }
    .hd {
      color: #888;
      > :last-child {
        visibility: visible;
      }
    }
  }
</style>



